- ÊîØ‰ªòÊàêÂäüÂêéÁ≥ªÁªü‰ºö‰∏ªÂä®ÂõûË∞ÉÂïÜÊà∑ÁöÑnotify_url
- ÂõûË∞ÉÂèÇÊï∞ÂåÖÂê´ËÆ¢ÂçïÁä∂ÊÄÅÂíåÁ≠æÂêç‰ø°ÊÅØ
- ÂïÜÊà∑Êî∂Âà∞ÂõûË∞ÉÂêéÈúÄË¶ÅËøîÂõû"success"Â≠óÁ¨¶‰∏≤Á°ÆËÆ§

